import {getFormatPagination} from '../utils/get-format-pagination';

const newsPage = () => {
  let recentlyWatchedSwiper;
  let mobile;
  const widthScreen = 480;

  const initSwiperMobile = () => {
    if (!document.querySelector('.recently-watched__swiper')) {
      return;
    }
    if (recentlyWatchedSwiper) {
      recentlyWatchedSwiper.destroy(true, true);
    }
    mobile = true;
    // eslint-disable-next-line no-undef, no-unused-vars
    recentlyWatchedSwiper = new Swiper('.recently-watched__swiper', {
      slideClass: 'catalog__column',
      slidesPerView: 'auto',
      spaceBetween: 14,
      pagination: {
        el: '.recently-watched__pagination',
        clickable: true,
        type: 'bullets',
      },
    });
  };

  const initSwiperNotMobile = () => {
    if (!document.querySelector('.recently-watched__swiper')) {
      return;
    }
    if (recentlyWatchedSwiper) {
      recentlyWatchedSwiper.destroy(true, true);
    }
    mobile = false;
    // eslint-disable-next-line no-undef, no-unused-vars
    recentlyWatchedSwiper = new Swiper('.recently-watched__swiper', {
      slideClass: 'catalog__column',
      slidesPerView: 'auto',
      spaceBetween: 14,
      pagination: {
        el: '.recently-watched__pagination',
        clickable: true,
        type: 'custom',
        renderCustom: getFormatPagination,
      },
      navigation: {
        nextEl: '.recently-watched__nav--next',
        prevEl: '.recently-watched__nav--prev',
      },
    });
  };

  window.addEventListener('resize', function () {
    if (window.innerWidth <= widthScreen && mobile) {
      return;
    }
    if (window.innerWidth > widthScreen && !mobile) {
      return;
    }
    if (window.innerWidth > widthScreen && mobile) {
      mobile = false;
      initSwiperNotMobile();
    }
    if (window.innerWidth <= widthScreen && !mobile) {
      mobile = true;
      initSwiperMobile();
    }
  }, false);

  if (window.innerWidth <= widthScreen) {
    initSwiperMobile();
  } else {
    initSwiperNotMobile();
  }
};

export {newsPage};
