import {initSwiper} from './modules/init-swiper';
import {tabs} from './modules/tabs';
import {dropdown} from './modules/dropdown';
import {mainPage} from './modules/main-page';
import {doSliderOnObjectPage} from './modules/slider-object-page';
import {dropdownLists} from './modules/dropdown-lists';
import {getPhone} from './modules/show-phone';
import {newsPage} from './modules/news-page';
