initSwiper('.offers__swiper', {
  slidesPerView: 'auto',
  spaceBetween: 15,
  freeMode: true,
}, true, 1200);

initSwiper('.reviews__swiper', {
  loop: true,
  centeredSlides: true,
  pagination: {
    el: '.swiper-pagination',
    type: 'fraction',
  },
  navigation: {
    nextEl: '.swiper-button-next',
    prevEl: '.swiper-button-prev',
  },
  slidesPerView: 1,
  spaceBetween: 15,
  breakpoints: {
    768: {
      slidesPerView: 2,
    },
    1028: {
      slidesPerView: 3,
    },
  },
}, false, 0);

initSwiper('.mission__slider', {
  slidesPerView: 1,
  pagination: {
    el: '.swiper-pagination',
    clickable: true,
  },
  navigation: {
    nextEl: '.swiper-button-next',
    prevEl: '.swiper-button-prev',
  },
}, false, 0);

initSwiper('.hero__navigation', {
  freeMode: true,
  slideClass: 'hero__item-navigation',
  slidesPerView: 'auto',
  spaceBetween: 15,
  navigation: {
    nextEl: '.hero__control--button-next',
    prevEl: '.hero__control--button-prev',
  },
}, true, 767);

initSwiper('.catalog-swiper__swiper-container', {
  spaceBetween: 30,
  pagination: {
    el: '.swiper-pagination',
    clickable: true,
  },
}, false, 0);
