const odjectCardSwiper = () => {
  // eslint-disable-next-line no-undef, no-unused-vars
  const slider = document.querySelector('.slider-gallery__main');
  const sliderThumbs = document.querySelector('.slider-gallery__thumbs');

  let mySwiperThumbs = new Swiper(sliderThumbs, {
    slidesPerView: 5,
    spaceBetween: 10,
    loopedSlides: 5,
    freeMode: true,
    watchSlidesVisibility: true,
    watchSlidesProgress: true,
    direction: 'vertical',
  });

  let mySwiper = new Swiper(slider, {
    spaceBetween: 0,
    loop: true,
    loopedSlides: 5,
    thumbs: {
      swiper: mySwiperThumbs,
    },
    direction: 'vertical',
  });
};

export {odjectCardSwiper};
