import {initSwiper} from './init-swiper';

const mainPage = () => {
  // eslint-disable-next-line no-undef, no-unused-vars
  const swiper = new Swiper('.promo__tabs', {
    navigation: {
      nextEl: '.promo__nav-btn--next',
      prevEl: '.promo__nav-btn--prev',
    },
    freeMode: true,
    slideClass: 'promo__tab',
    slidesPerView: 'auto',
  });

  // eslint-disable-next-line no-undef, no-unused-vars
  const swiper3 = new Swiper('.buy-online__tabs', {
    navigation: {
      nextEl: '.buy-online__nav-btn--next',
      prevEl: '.buy-online__nav-btn--prev',
    },
    freeMode: true,
    slideClass: 'buy-online__tab',
    slidesPerView: 'auto',
  });

  const filterBlocks = document.querySelectorAll('.promo-filter');

  if (filterBlocks) {
    filterBlocks.forEach((block) => {
      const btn = block.querySelector('.promo-filter__more');

      if (btn) {
        btn.addEventListener('click', () => {
          block.classList.toggle('promo-filter--open');
        });
      }
    });
  }

  // eslint-disable-next-line no-undef
  const swiper2 = new Swiper('.consult__slider', {
    slideClass: 'consult__slide',
    slidesPerView: 1.3,
    watchSlidesVisibility: true,
    watchSlidesProgress: true,
    slideToClickedSlide: true,
    spaceBetween: -100,
    loop: true,
    loopAdditionalSlides: 2,
    // slidesOffsetBefore: 10,
    // slidesOffsetAfter: 10,
    navigation: {
      nextEl: '.consult__nav--next',
      prevEl: '.consult__nav--prev',
    },

    breakpoints: {
      768: {
        slidesPerView: 3,
        spaceBetween: -140,
        // simulateTouch: false,
        centeredSlides: true,
        allowTouchMove: false,
      },
    },
  });

  document.querySelectorAll('.consult__btns-to').forEach((btn) => {
    btn.addEventListener('click', () => {
      swiper2.slideTo(btn.dataset.slide);
    });
  });

  initSwiper('.buy-online__block', {
    slideClass: 'catalog__column',
    slidesPerView: 'auto',
    spaceBetween: 14,
    pagination: {
      el: '.buy-online__pagination',
      clickable: true,
    },

    breakpoints: {
      1024: {
        pagination: {
          el: false,
        },
      },
    },
  }, true, 1023);

  initSwiper('.day-offers__list', {
    slideClass: 'catalog__column',
    slidesPerView: 'auto',
    spaceBetween: 14,
    pagination: {
      el: '.day-offers__pagination',
      clickable: true,
    },

    breakpoints: {
      768: {
        pagination: {
          el: false,
        },
      },
    },
  }, true, 1366);

  initSwiper('.news__wrap', {
    slideClass: 'news__item',
    slidesPerView: 'auto',
    spaceBetween: 10,
    pagination: {
      el: '.swiper-pagination',
      clickable: true,
    },
    breakpoints: {
      768: {
        pagination: {
          el: false,
        },
      },
    },
  }, true, 767);
};

export {mainPage};
