const getFormatPagination = (swiperNews, current, total) => {
  let string = '';
  if (total < 7) {
    for (let i = 1; i <= total; i++) {
      if (i === current) {
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet swiper-pagination-bullet-active">' + i + '</span>';
      } else {
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + i + '</span>';
      }
    }
    return string;
  } else {
    switch (current) {
      case 1:
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet swiper-pagination-bullet-active">' + 1 + '</span>';
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + 2 + '</span>';
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + 3 + '</span>';
        string += '<span class="recently-watched__pagination-bullet recently-watched__pagination-bullet--points">' + '...' + '</span>';
        for (let i = 5; i < total; i++) {
          string += '<span class="swiper-pagination-bullet visually-hidden">' + i + '</span>';
        }
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + total + '</span>';
        break;

      case 2:
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + 1 + '</span>';
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet swiper-pagination-bullet-active">' + 2 + '</span>';
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + 3 + '</span>';
        string += '<span class="recently-watched__pagination-bullet recently-watched__pagination-bullet--points">' + '...' + '</span>';
        for (let i = 5; i < total; i++) {
          string += '<span class="swiper-pagination-bullet visually-hidden">' + i + '</span>';
        }
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + total + '</span>';
        break;

      case 3:
        string += '<span class=" recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + 1 + '</span>';
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + 2 + '</span>';
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet swiper-pagination-bullet-active">' + 3 + '</span>';
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + 4 + '</span>';
        string += '<span class="recently-watched__pagination-bullet recently-watched__pagination-bullet--points">' + '...' + '</span>';
        for (let i = 6; i < total; i++) {
          string += '<span class="swiper-pagination-bullet visually-hidden">' + i + '</span>';
        }
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + total + '</span>';
        break;

      case (total - 2):
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + 1 + '</span>';
        string += '<span class="recently-watched__pagination-bullet recently-watched__pagination-bullet--points">' + '...' + '</span>';
        for (let i = 3; i < total - 3; i++) {
          string += '<span class="swiper-pagination-bullet visually-hidden">' + i + '</span>';
        }
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + (total - 3) + '</span>';
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet swiper-pagination-bullet-active">' + (total - 2) + '</span>';
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + (total - 1) + '</span>';
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + total + '</span>';
        break;

      case (total - 1):
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + 1 + '</span>';
        string += '<span class="recently-watched__pagination-bullet recently-watched__pagination-bullet--points">' + '...' + '</span>';
        for (let i = 3; i < total - 2; i++) {
          string += '<span class="swiper-pagination-bullet visually-hidden">' + i + '</span>';
        }
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + (total - 2) + '</span>';
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet swiper-pagination-bullet-active">' + (total - 1) + '</span>';
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + total + '</span>';
        break;
      case total:
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + 1 + '</span>';
        string += '<span class="recently-watched__pagination-bullet recently-watched__pagination-bullet--points">' + '...' + '</span>';
        for (let i = 3; i < total - 2; i++) {
          string += '<span class="swiper-pagination-bullet visually-hidden">' + i + '</span>';
        }
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + (total - 2) + '</span>';
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + (total - 1) + '</span>';
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet swiper-pagination-bullet-active">' + total + '</span>';
        break;

      default:
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + 1 + '</span>';
        string += '<span class="recently-watched__pagination-bullet recently-watched__pagination-bullet--points">' + '...' + '</span>';
        for (let i = 3; i < current - 1; i++) {
          string += '<span class="swiper-pagination-bullet visually-hidden">' + i + '</span>';
        }
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + (current - 1) + '</span>';
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet swiper-pagination-bullet-active">' + current + '</span>';
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + (current + 1) + '</span>';
        for (let i = current + 2; i < total - 1; i++) {
          string += '<span class="swiper-pagination-bullet visually-hidden">' + i + '</span>';
        }
        string += '<span class="recently-watched__pagination-bullet recently-watched__pagination-bullet--points">' + '...' + '</span>';
        string += '<span class="recently-watched__pagination-bullet swiper-pagination-bullet" tabindex="0">' + total + '</span>';
        break;
    }
    return string;
  }
};

export {getFormatPagination};
